<template>
	<view class="content">
		<view class="img"><image src="../../static/img3.png" mode="aspectFit"></image></view>
		<view class="title">设备物联孪生定位系统</view>
		<view class="scan"><view class="button" @click="scan">扫一扫 登记设备</view></view>
		<view class="log">
			<view class="item" v-for="(item, index) in list" :key="index" @click="toDetail(item.info.patientno)">
				<view>{{ item.info.detail.jbxx1 }}</view>
				<view>
					<u-icon name="map-fill" class="u-m-r-10"></u-icon>
					{{ item.info.detail.jbxx6 }}
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			form: {},
			now: '',
			overModal: false,
			list: []
		};
	},
	onLoad() {
		this.getList();
	},
	methods: {
		getList() {
			wx.request({
				url: 'https://sbzhgl.ifufan.com:4000/devices',
				method: 'GET',
				data: {},
				success: res => {
					if (res.data.code == 200) {
						this.list = res.data.data;
					}
				}
			});
		},

		toDetail(id) {
			uni.navigateTo({
				url: '/pages/index/index?id=' + id
			});
		},

		scan() {
			uni.scanCode({
				scanType: 'qrCode',
				success: function(res) {
					console.log(res)
					uni.navigateTo({
						url:'/' + res.path
					})
				}
			});
		}
	}
};
</script>

<style scoped lang="less">
.content {
	width: 100%;
}

.img {
	text-align: center;
	margin-top: 50upx;

	image {
		width: 750upx;
		height: 750upx;
	}
}

.title {
	font-size: 50upx;
	font-weight: 700;
	margin-top: -50upx;
	color: #fff;
	text-align: center;
}

.scan {
	text-align: center;

	.button {
		margin: 50upx 0 20upx;
		padding: 20upx 50upx;
		color: #fff;
		font-size: 40upx;
		font-weight: 700;
		background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
		border-radius: 50upx;
		display: inline-block;
		border: 3upx solid rgba(255, 255, 255, 0.5);
	}
}

.log {
	margin: 30upx;
	.item {
		background: rgba(255, 255, 255, 0.3);
		padding: 20upx;
		margin-bottom: 20upx;
		border-radius: 10upx;
		color: #fff;
		font-size: 30upx;
		display: flex;
		justify-content: space-between;
		border: 1px solid rgba(255, 255, 255, 0.3);
	}
}
</style>
